	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-01-11-the-inflector-module-another-reason-to-love-rails/index.md absPath of file >>> MarkdownRemark","html":"<p>I love how when you need something in Rails the solution is usually already in place.  For instance in the <a href=\"http://apps.facebook.com/indulgences/\">indulgenc.es</a> application I have the need to create a rank with 1st, 2nd, 3rd etc.  In most frameworks you would have to write this functionality yourself or if you are very lucky you might find something on the Internet.  In Rails you do this:</p>\n<pre><code class=\"ruby\">\nnumber.ordinalize\n</pre></code>\n<p>Then 1 becomes 1st, 2 becomes 2nd, 1233 becomes 1233rd etc.</p>\n<p>I am still smiling</p>","frontmatter":{"title":"The Inflector Module &#8211; Another Reason to Love Rails","date":"January 11, 2008","imageUrl":null,"image":null}}},"pathContext":{"path":"/the-inflector-module-another-reason-to-love-rails"}}

